
























import validate from '../../common/validate.js';
import common from '../../common/common.js';
import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			username: '',
			password: ''
		}
	},

	methods: {
		...mapMutations(['login']),
		goback: function() {
			uni.navigateBack({
				delta: 1
			})
		},
		bindlogin: function() {
			if (!validate.validateUsername(this.username)) {
				uni.showToast({
					icon: 'none',
					title: '账号必须为6-15位，字母和数字组合'
				});
				return;
			}
			if (!validate.validateUsername(this.password)) {
				uni.showToast({
					icon: 'none',
					title: '密码必须为6-15位，字母和数字组合'
				});
				return;
			}

			let data = {
				'username': this.username,
				'password': this.password
			};
			var that = this;
			uni.request({
				url: common.hosturl + "login/dologin.html",
				method: "post",
				header: {
					'content-type': 'application/x-www-form-urlencoded' //自定义请求头信息
				},
				data: data,
				success: function(res) {
					if (res.data.code == 0) {
						let regdata = {
							token: res.data.token
						};
						that.toMain(regdata);
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.msg
						});

					}

				},
				fail: function() {
					uni.showToast({
						icon: 'none',
						title: '网络错误'
					});
				}

			})
		},
		toMain: function(user) {
			this.login(user);
			uni.showToast({
				title: '登录成功',
				success: function() {
					uni.reLaunch({
						url: "../../pages/manage/manage"
					});
				}
			});

		}
	}
}



